import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import test1 from '../../images/testimonials/img-1.png'
import test2 from '../../images/testimonials/img-2.png'
import test3 from '../../images/testimonials/img-3.png'
import test4 from '../../images/ts.png'

import './style.css'

class Testimonial extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nav1: null,
            nav2: null
        };
    }

    componentDidMount() {
        this.setState({
            nav1: this.slider1,
            nav2: this.slider2
        });
    }

    render() {
        return (
            <section className="testimonials-section section-padding">
                <div className="container">
                    <div className="row">
                        <div className="col col-xs-12">
                            <div className="testimonials-slider">
                                {/* Images du slider */}
                                <div className="testimonial-thumb-active">
                                    <Slider
                                        asNavFor={this.state.nav2}
                                        fade={true}
                                        prevArrow={<button type="button" className="slick-prev"><i className="fa fa-angle-double-left"></i></button>}
                                        nextArrow={<button type="button" className="slick-next"><i className="fa fa-angle-double-right"></i></button>}
                                        ref={slider => (this.slider1 = slider)}
                                    >
                                        <div className="test-img">
                                            <img src={test1} alt="Tmoignage 1 - Maison des Associations de Gudiawaye" />
                                        </div>
                                        <div className="test-img">
                                            <img src={test2} alt="Tmoignage 2 - Maison des Associations de Gudiawaye" />
                                        </div>
                                    </Slider>
                                </div>

                                {/* Contenu des tmoignages */}
                                <div className="testimonial-content-active text-center">
                                    <Slider
                                        asNavFor={this.state.nav1}
                                        ref={slider => (this.slider2 = slider)}
                                        slidesToShow={1}
                                        swipeToSlide={true}
                                        arrows={false}
                                        fade={true}
                                    >
                                        {/* Tmoignage 1 */}
                                        <div className="grid">
                                            <ul>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><span><i className="fa fa-star"></i></span></li>
                                                <li><span><i className="fa fa-star"></i></span></li>
                                            </ul>
                                            <p>
                                                Grce  la Maison des Associations de Gudiawaye, notre organisation a pu bnficier dun espace de travail moderne,
                                                de formations adaptes et dun accompagnement personnalis. Aujourdhui, nous collaborons avec dautres structures
                                                locales pour renforcer notre impact social.
                                            </p>
                                            <div className="info">
                                                <h5>Ndye Fatou Faye</h5>
                                                <p>Prsidente de lassociation Femmes Solidaires de Gudiawaye</p>
                                            </div>
                                        </div>

                                        {/* Tmoignage 2 */}
                                        <div className="grid">
                                            <ul>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><i className="fa fa-star"></i></li>
                                                <li><span><i className="fa fa-star"></i></span></li>
                                                <li><span><i className="fa fa-star"></i></span></li>
                                            </ul>
                                            <p>
                                                En tant que jeune entrepreneur, jai trouv  la MAG un cadre stimulant pour dvelopper mon projet numrique.
                                                Les conseils reus et le rseau de partenaires mont permis de passer de lide  la concrtisation.
                                                Cest une vritable chance pour les jeunes de Gudiawaye.
                                            </p>
                                            <div className="info">
                                                <h5>Mamadou Ba</h5>
                                                <p>Fondateur de la start-up JmmTech</p>
                                            </div>
                                        </div>
                                    </Slider>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Dcorations visuelles */}
                <div className="testi-shape">
                    <img src={test3} alt="Dcor Maison des Associations" />
                </div>
                <div className="testi-shape2">
                    <img src={test4} alt="Dcor secondaire" />
                </div>
            </section>
        );
    }
}

export default Testimonial;
